import { Component, OnInit } from '@angular/core';
import {ProddataService} from "../proddata.service";
import {ActivatedRoute} from "@angular/router";
@Component({
  selector: 'app-prod-list',
  templateUrl: './prod-list.component.html',
  styleUrls: ['./prod-list.component.css']
})
export class ProdListComponent implements OnInit {
  dataListing: Array<Object>= []
  prodObj: any= {};
  constructor(private dataservice: ProddataService, private activatedRoute: ActivatedRoute) { }

  ngOnInit(): void {
    console.log(this.activatedRoute.snapshot.params.id)
    this.dataListing= this.dataservice.getAllProducts();
    this.prodObj= this.dataservice.getProdById(this.activatedRoute.snapshot.params.id)
  }

}
