import { Component, Input, OnInit } from '@angular/core';
import {ActivatedRoute} from "@angular/router";
import {UserdataService} from "../userdata.service"
@Component({
  selector: 'app-usercard',
  templateUrl: './usercard.component.html',
  styleUrls: ['./usercard.component.css']
})
export class UsercardComponent implements OnInit {
  @Input() userData: any={}
  userObj: any={}
  constructor(private dataservice: UserdataService, private activatedRoute: ActivatedRoute) { }

  ngOnInit(): void {
    this.userObj= this.dataservice.getUserById(this.activatedRoute.snapshot.params.id)
  }
  deleteUserObj(id: number)
  {
    this.dataservice.removeUser(id);
  }
}
