import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class ProddataService {
  data= [
    {
      id: 1,
      name: "Item 1",
      image: "http://placehold.it/200x200",
      description: "ITEM 1 DESCRIPTION",
      price: "$250"
    },
    {
      id: 2,
      name: "Item 2",
      image: "http://placehold.it/200x200",
      description: "ITEM 2 DESCRIPTION",
      price: "$250"
    },
    {
      id: 3,
      name: "Item 3",
      image: "http://placehold.it/200x200",
      description: "ITEM 3 DESCRIPTION",
      price: "$350"
    },
    {
      id: 4,
      name: "Item 4",
      image: "http://placehold.it/200x200",
      description: "ITEM 4 DESCRIPTION",
      price: "$450"
    },
    {
      id: 5,
      name: "Item 5",
      image: "http://placehold.it/200x200",
      description: "ITEM 5 DESCRIPTION",
      price: "$550"
    },
    {
      id: 6,
      name: "Item 6",
      image: "http://placehold.it/200x200",
      description: "ITEM 6 DESCRIPTION",
      price: "$650"
    }
  ]

  constructor() { }

  getAllProducts():Array<Object>
  {
    return this.data;
  }

  getProdById(id: number)
  {
    return this.data.find(p => p.id== id);
  }

  getProdSize()
  {
    return this.data.length;
  }
  
  addProd(dataObj:any)
  {
    dataObj.id= this.data.length +1;
    dataObj.image= "http://placehold.it/200x200"
    this.data.push(dataObj);
  }
  removeProd(id: number)
  { 
    this.data.splice(id-1,1);
  }
  editProd(id: number, dataObj:any)
  {
    let findObj=this.data.findIndex((obj) => {
      return obj.id==id;
    });
    this.data[findObj]= dataObj;
  }
}
