import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CreateprodComponent } from './createprod/createprod.component';
import { CreateuserComponent } from './createuser/createuser.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { EditProdComponent } from './edit-prod/edit-prod.component';
import { EditUserComponent } from './edit-user/edit-user.component';
import { ProdListComponent } from './prod-list/prod-list.component';
import { UserListComponent } from './user-list/user-list.component';

const routes: Routes = [
  {
    path: 'createprod',
    component: CreateprodComponent
  },
  {
    path: 'createuser',
    component: CreateuserComponent
  },
  {
    path: 'prodList',
    component: ProdListComponent
  },
  {
    path: 'userList',
    component: UserListComponent
  },
  {
    path: 'dashboard',
    component: DashboardComponent
  },
  {
    path: 'editProd/:id',
    component: EditProdComponent
  },
  {
    path: 'editUser/:id',
    component: EditUserComponent
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
