import { Component, Input, OnInit } from '@angular/core';
import {ProddataService} from "../proddata.service";
import {ActivatedRoute} from "@angular/router"
@Component({
  selector: 'app-card',
  templateUrl: './card.component.html',
  styleUrls: ['./card.component.css']
})
export class CardComponent implements OnInit {
  @Input() prodData: any={}
  prodObj: any={}
  constructor(private dataservice: ProddataService, private activatedRoute: ActivatedRoute) { }

  ngOnInit(): void {
    console.log(this.activatedRoute.snapshot.params.id)
    this.prodObj= this.dataservice.getProdById(this.activatedRoute.snapshot.params.id)
  }
  deleteProdObj(id: number)
  {
    this.dataservice.removeProd(id);
  }
}
