import { Component, OnInit } from '@angular/core';
import {UserdataService} from "../userdata.service";
import {ProddataService} from "../proddata.service";
@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
  prodL: any=0;
  userL: any=0;

  constructor(private userservice: UserdataService, private prodservice: ProddataService) { }
  ngOnInit(): void {
    this.prodL= this.prodservice.getProdSize();
    this.userL= this.userservice.getUserSize();
  }

}
