import { Component, OnInit } from '@angular/core';
import {FormBuilder, Validators} from "@angular/forms"
import {ProddataService} from "../proddata.service";
import {Router} from "@angular/router"
@Component({
  selector: 'app-createprod',
  templateUrl: './createprod.component.html',
  styleUrls: ['./createprod.component.css']
})
export class CreateprodComponent implements OnInit {
productFormGroup: any;
  constructor(private fb: FormBuilder, private dataservice: ProddataService, private router: Router) { }

  ngOnInit(): void {
    this.productFormGroup= this.fb.group({
      name: this.fb.control('',[Validators.required,Validators.minLength(5),Validators.maxLength(15)]),
      price: this.fb.control('',Validators.required),
      description: this.fb.control('',[Validators.required,Validators.minLength(10),Validators.maxLength(50)])
    })
  }

  submitForm()
  {
    console.log(this.productFormGroup.value);
    this.dataservice.addProd(this.productFormGroup.value)
    this.productFormGroup.reset();
    this.router.navigate(['prodList'])
  }

}
